.formContent{
    display: flex;
    flex-direction: column;
    align-items: center;
    
    label{
        font-size: 1.5rem;
        text-shadow: 2px 2px 2px rgb(35, 34, 34);
    }
    input{
        border-radius: 60px;
        padding: 10px;
        width: 160px;
        margin-top: 20px;
        text-align: center;
        font-size: 1.2rem;
        margin-bottom: 20px;
        &:hover{
            background-color: black;
            color: whitesmoke;
        }
    }
}
.cardDisplay{
    display: flex;
    flex-wrap: wrap;
}