.headerForm form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 50px;

    label{
        font-size: 2.5rem;
        font-weight: bold;
        margin: 10px;
    }
    input{
        width: 200px;
        height: 30px;
        border-radius: 50px;
        background:  rgb(35, 34, 34);
        color: whitesmoke;
        margin: 10px;
        font-size: 1.5rem;
        text-align: center;
        &:hover{
            background: whitesmoke;
            color: rgb(35, 34, 34);
        }

    }
    button{
        margin: 20px;
        width: 150px;
        height: 50px;
        border-radius: 50px;
        font-size: 1.5rem;
        cursor: pointer;
        color: rgb(35, 34, 34);
        font-weight: bold;
        &:hover{
            background: rgb(35, 34, 34);;
            color: whitesmoke;
        }
    }
}
