@font-face {
    font-family: "font-1" ;
    src: url(../assets/fonts/Questrial-Regular.ttf);
}

@font-face {
    font-family: "font-2";
    src: url(../assets/fonts/Michroma-Regular.ttf);;
}

$font1 : 'font-1',sans-serif;
$font2 : "font-2",serif;

$color1: grey;
$color2: whitesmoke;
$color3: rgb(35, 34, 34);

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    outline: none;
    border: none;
}

body{
    font-family: $font1;
    color: $color3;
    background-color: $color1;
}

h3{
    font-family: $font2;
}

button{
    font-family: $font2;
}