.cardContainer{
    flex: 1 0 20%;
    margin: 10px;
    border: 5px solid whitesmoke;
    border-radius: 15px;
    text-align: center;
    padding: 5px;

    img{
        width: 100%;
    }
    h1{
        font-size: 2.5rem;
        color: whitesmoke;
    }
    h3{
        font-size: 2rem;
        color: black;
    }
    p{
        font-size: 1.5rem;
        text-align: justify;
        letter-spacing: 2px;
        padding: 3px;
        margin: 0 auto;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 10;
        overflow: hidden;

    }
    button{
        margin: 10px;
        padding: 8px;
        width: 75%;
        border-radius: 30px;
        cursor: pointer;
        font-size: 1.2rem ;

        &:hover{
            background: black;
            color: grey;
        }
    }
}